          alt="Vuetify Logo"
          class="shrink mr-2 mt-n2"
          contain
          :src="require('./assets/logo.png')"
          transition="scale-transition"

          width="60"
        />

        <!-- <v-img
          alt="Vuetify Name"
          class="shrink mt-1 hidden-sm-and-down"
          contain
