      <v-btn icon dark @click.stop="dialog = true">
        <v-icon>mdi-information</v-icon>
      </v-btn>

    </v-app-bar>

    <v-main>
      <!-- <v-btn @click="play" /> -->
      <router-view :returnHomeThis="returnHomeThis" v-if="isLoaded" />

      <span v-else>
        <div v-if="isDownloading" class="text-center">
          <v-progress-circular
            rotate="-90"
            :value="progressCircular"
            color="primary"
            class="ma-3"
          ></v-progress-circular>
          <br />{{ progressStr }}
        </div>
      </span>

      <v-dialog v-model="dialog" max-width="500">
        <v-card class="pt-4">
          <v-card-text>
            <strong>検索例</strong><br>
            <br>
            丈槍 由紀の場合<br>
            1.丈槍 由紀 (フルネーム)<br>
            2.ゆき (内部ニックネーム)<br>
            3.yuki (内部英名)<br>
            4.水瀬いのり (声優)<br>
            5.gakkou (内部英作品名)<br>
            <br>
            ジンジャーの場合<br>
            1.ジンジャー<br>
            2.渕上舞<br>
