<script>
// import HelloWorld from './components/HelloWorld';
import localforage from "localforage";
import axios from "axios";

export default {
  name: "App",

  data: () => ({
    isLoaded: false,

    db: {},

    localforage: localforage,
    axios: axios,
    voice_URL_list: {},

    downloadStatus: {
      downloaded: 0,
      max: 0,
    },
    isDownloading: false,

    dialog:false,
  }),

  computed: {
    progressCircular() {
      const dl_status = this.downloadStatus;
      return (dl_status.downloaded / dl_status.max) * 100;
    },
    progressStr() {
      const dl_status = this.downloadStatus;
      return `${dl_status.downloaded} / ${dl_status.max}`;
    },
    returnHomeThis(){
      return this;
    }
  },

  methods: {
    play() {
      let url = this.c;
      let audio = new Audio(url);
      audio.volume = 0.5;
      // audio.addEventListener('ended', function() {
      //   delete audio;
      //   next_music();
      // });
      audio.play();
    },
    
  },

  async mounted() {
    let vm = this;
    this.db = (
      await this.axios.get(
        "https://database.kirafan.cn/database/NamedList.json?t=1613393063603"
      )
    ).data;

    this.downloadStatus.max = this.db.length;

    this.isDownloading = true;

    async function getCacheVoice(id) {
      let voiceData = await vm.localforage.getItem(id);
      // mime 	audio/mpeg
      if (voiceData == null) {
        voiceData = (
          await vm.axios.get(`./downloadedVoice/voice_${id}_000_0.mp3`, {
            responseType: "blob",
          })
        ).data;
        localforage.setItem(id, voiceData);
      }
      vm.voice_URL_list[id] = URL.createObjectURL(voiceData);

      vm.downloadStatus.downloaded++;
      // return URL.createObjectURL(voiceData)
    }

    let prom_voiceDL = this.db.map((x) => {
      let id = x["m_ResouceBaseName"];
      return getCacheVoice(id);
    });

    await Promise.all(prom_voiceDL);

    this.isLoaded = true;
  },
};
</script>
