<template>
  <v-app>
    <div>5問中{{ correct }}問正解！</div>
    <div>
      <v-row>
        <v-col>
          <v-btn
            @click="
              () => {
                jumpTwitter();
              }
            "
            class="btn"
            >ツイートする</v-btn
          >
        </v-col>
        <v-col>
          <v-btn
            @click="
              () => {
                replay();
              }
            "
            class="btn"
            >もう一度プレイ</v-btn
          >

        </v-col>
      </v-row>
    </div>
    <div></div>
  </v-app>
</template>

<style scoped>
    .btn{
        width:100%
    }
</style>

<script>
export default {
  props: ["correct", "returnStartPageThis"],
  methods: {
    jumpTwitter() {
      //eslint-disable-next-line
      globalThis.location.href = encodeURI(
        `https://twitter.com/intent/tweet?text=きらファンボイスクイズで5問中${this.correct}問正解しました！\nhttps://y52en.github.io/kirafan-voice-quiz/`
      );
    },
    replay() {
      // returnStartPageThis.
      this.returnStartPageThis.mode = this.returnStartPageThis.modeList.main;
    },
  },
};
</script>