<template>
  <v-app>
    <v-row>
      <v-col cols="2" class="fix mt-2">
        <v-btn outlined color="indigo" @click="voicePlay()">
          play <v-icon dark right> mdi-play </v-icon> 
        </v-btn>
      </v-col>
      <v-col cols="5" class="fix text-right"
        >残り{{ remainTime }} 秒 <br />
        {{ now_quiz }} / {{ max_quiz }} 問目
      </v-col>
      <!-- <span > -->

      <v-col v-if="question === false && now_quiz <= 4" cols="5" class="fix mt-2">
        <v-btn outlined color="indigo"  @click="() => makeQuiz()"> 次の問題 </v-btn>
      </v-col>
      <v-col v-if="question === false && now_quiz === 5" cols="5" class="fix mt-2">
        <v-btn outlined color="indigo"  @click="() => jumpResultPage()"> リザルトへ </v-btn>
      </v-col>

      <v-col v-if="question" cols="5" class="fix">
        <v-text-field label="検索"  placeholder="enterで検索" @keydown.enter="search"></v-text-field>
      </v-col>
      <v-col v-if="question" style="height: 400px">
        <v-card width="80%" style="position: absolute; top: 65px">
          <v-list>
            <v-list-item-group v-model="selected">
              <v-list-item>
                <v-list-item-content>
                  <v-list-item-title @click="() => click('pass')"
                    >この問題をパス</v-list-item-title
                  >
                </v-list-item-content>
              </v-list-item>
              <v-list-item
                v-for="(item, i) in this.questionList"
                :key="i"
                @click="() => click(item)"
                :color="item.color"
              >
                <v-list-item-content>
                  <v-list-item-title
                    v-text="item.m_FullName"
                  ></v-list-item-title>
                </v-list-item-content>
              </v-list-item>
            </v-list-item-group>
          </v-list>
        </v-card>
      </v-col>
      <!-- </span> -->
      <v-col class="fix" style="position: absolute; top: 50px" v-else>
        <span class="font-weight-medium text-h5">
          {{ text }}
        </span>
      </v-col>
    </v-row>
    <span class="quiz_img" v-if="img_correct" text-align="center">
      <v-img :src="require('../assets/correct.svg')"></v-img>
    </span>
    <span class="quiz_img" v-if="img_wrong" text-align="center">
      <v-img :src="require('../assets/wrong.svg')"></v-img>
    </span>
  </v-app>
</template>

<style>
.fix {
  height: 100px;

}
.quiz_img {
  width: calc(100vmin - 60px);
  position: fixed;
  top: calc(50% - (100vmin - 60px) / 2 + 20px);
  left: calc(50% - (100vmin - 60px) / 2);
}
</style>

<script>
export default {
  props: ["returnStartPageThis"],
  data: () => ({
    selected: undefined,

    correct: undefined,
    questionList: [],

    question: true,
    text: "",

    remainQuestionable: [],

    // searchList: [],

    remainTime: 180,
    interval_id: 0,

    now_correcting: 0,
    now_quiz: 0,
    max_quiz: 5,

    img_correct: false,
    img_wrong: false,
    img_interval_id: 0,
  }),

  methods: {
    click(item) {
      const dispTime = 1200;
      scrollTo(0, 0);
      clearInterval(this.interval_id);
      if (item === "pass") {
        this.text = "";
        wrong.bind(this)();
      } else if (item === "timeover") {
        this.text = "時間切れ！";
        wrong.bind(this)();
      } else if (
        item.m_NamedType === this.remainQuestionable[this.correct].m_NamedType
      ) {
        // this.$set(item, "color", "blue");
        this.text = "正解！";
        this.img_correct = true;
        setTimeout(() => (this.img_correct = false), dispTime);
        this.now_correcting++;
      } else {
        // this.$set(item, "color", "red");
        this.text = "不正解...";
        wrong.bind(this)();
      }
      this.text +=
        "\n答えは" + this.remainQuestionable[this.correct].m_FullName;

      this.question = false;

      // let vm = this
      function wrong() {
        this.img_wrong = true;
        setTimeout(() => (this.img_wrong = false), dispTime);
      }
    },
    random(length) {
      return Math.floor(Math.random() * length);
    },
    search(e) {
      const str = e.target.value;
      // this.questionList = [];
      const searchRegexp = new RegExp(str, "i");
      const check_mutch = (x) => searchRegexp.test(x);
      this.questionList = this.returnStartPageThis.db.filter(
        (element) =>
          check_mutch(element.m_NickName) ||
          check_mutch(element.m_FullName) ||
          check_mutch(element.m_ResouceBaseName) ||
          check_mutch(element.m_CVText) ||
          check_mutch(element.fullName)
      );
    },
    setCountdown() {
      this.interval_id = setInterval(() => {
        if (this.remainTime === 1) {
          clearInterval(this.interval_id);
          this.click("timeover");
        }
        this.remainTime--;
      }, 1000);
    },
    jumpResultPage() {
      // console.log(this.now_correcting);
      this.returnStartPageThis.correct = this.now_correcting;
      this.returnStartPageThis.mode = this.returnStartPageThis.modeList.results;
    },
    voicePlay() {
      this.returnStartPageThis.play(
        this.remainQuestionable[this.correct]["m_ResouceBaseName"]
      );
    },
    makeQuiz() {
      if(this.correct !== undefined){
      this.remainQuestionable.splice(this.correct, 1);

      }
      let homeVM = this.returnStartPageThis;
      // console.log(homeVM.remainQuestionable);

      let ramdom = this.random(homeVM.remainQuestionable.length);

      this.correct = ramdom;

      this.now_quiz++;

      this.selected = undefined;
      this.questionList = homeVM.db;
      this.question = true;
      this.remainTime = 120;
      this.setCountdown();
      this.voicePlay();
    },
    init() {
      this.remainQuestionable = this.returnStartPageThis.remainQuestionable;
      this.now_correcting = 0;
      this.now_quiz = 0;
      this.max_quiz = 5;
      this.makeQuiz();
    },
  },
  mounted() {
    this.init();
    // homeVM.remainQuestionable[this.correct];
    // homeVM.remainQuestionable.splice(ramdom, 1);
    // for (let i = 0; i < 4; i++) {
    //   let random = this.random(homeVM.remainQuestionable.length);
    //   let ramdom_get = homeVM.remainQuestionable[random];
    //   ramdom_get.color = this.correct === i ? "blue" : "red";
    //   this.questionList.push(ramdom_get);
    //   homeVM.remainQuestionable.splice(random, 1);
    // }
    // console.log(this.questionList,homeVM.remainQuestionable);
  },
};
</script>